
body {
    color: #000;
    overflow-x: hidden;
    height: 100%;
    background-image: url("images/green-soft.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.card {
  padding: 30px 40px;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 20px;
  opacity: 0.7;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.2);

}


.blue-text {
  color: #00BCD4
}

.form-control-label {
  margin-bottom: 0
}

input,
textarea,
button {
  padding: 8px 15px;
  border-radius: 5px !important;
  margin: 5px 0px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  font-size: 18px !important;
  font-weight: 300
}

input:focus,
textarea:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  border: 1px solid #00BCD4;
  outline-width: 0;
  font-weight: 400
}

.btn-block {
  text-transform: uppercase;
  font-size: 15px !important;
  font-weight: 400;
  height: 43px;
  cursor: pointer
}

.btn-block:hover {
  color: #fff !important
}

button:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  outline-width: 0
}

.icon-play{
background-image : url("images/bsc.png");
background-size: cover;
display: inline-block;
margin-right: 5px;
vertical-align: text-top;
height: 19px;
width: 19px;
}

body {
height: 150%;
}

.meta-gray {
-webkit-filter: grayscale(1);
}
.meta-normal {
-webkit-filter: grayscale(0);
}















.board-row {
  content: "";
  display: table;
}

.board-col {
  content: "";
  display: table;
  float: left;
  height: 300px;
}

.status {
  margin-bottom: 10px;
}

.square {
  border: 2px solid white;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 54px;
  height: 57px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 57px;

  cursor: url("images/icons8-chip-32-blue.png"), auto;
}

.square-small{

  font-size: 17px !important;
  line-height: 33px !important;
  height: 35px !important;
  width: 35px !important;
  cursor: pointer !important;

}

.square:hover{
  background-color: yellowgreen;
}

.black {
    background: black;
    color: white;
}

.red {
    background: red;
    color: white;
}

.green {
  background: green;
  color: white;
  height: 171px;
  line-height: 170px;
  border-top-left-radius: 15px !important;
  border-bottom-left-radius: 15px !important;
}

.twoto1 {
  background: green;
  color: white;
  writing-mode: vertical-rl;
  font-size: 18px;
  border-top-right-radius: 5px !important;
  border-bottom-right-radius: 5px !important;
}

.ternary {
  background: green;
  color: white;
  font-size: 18px;
  width: 225px;
}

.sext1 {
  background: green;
  color: white;
  font-size: 18px;
  width: 113px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  background: transparent;
}

.game-board{
  flex-direction: row;
  padding-left: 20px;
  padding-top: 30px;
  background-color: transparent;
}

.game-table{
  width: 875px !important;
  max-width: max-content !important;
}

.game-info {
  margin: 10px;
  background-color: white;
  opacity: 0.7;
  padding: 10px;
}

.rombo-red {
  width: 37px; 
  height: 37px; 
  border: 3px solid white; 
  background: red;
  margin-left: 35px;
  margin-top: 7px;

  -webkit-transform: rotate(45deg) skew(345deg, 345deg);
  -moz-transform: rotate(45deg) skew(345deg, 345deg);
  -ms-transform: rotate(45deg) skew(345deg, 345deg);
  -o-transform: rotate(45deg) skew(345deg, 345deg);
  transform: rotate(45deg) skew(345deg, 345deg);
}

.rombo-black {
  width: 37px; 
  height: 37px; 
  border: 3px solid white; 
  background: black;
  margin-left: 35px;
  margin-top: 7px;

  -webkit-transform: rotate(45deg) skew(345deg, 345deg);
  -moz-transform: rotate(45deg) skew(345deg, 345deg);
  -ms-transform: rotate(45deg) skew(345deg, 345deg);
  -o-transform: rotate(45deg) skew(345deg, 345deg);
  transform: rotate(45deg) skew(345deg, 345deg);
}

.chips-board {
  content: "";
  display: table;
  padding-top: 30px;
  padding-left: 150px;
}

.chip {
  float: left;
  font-size: 20px;
  font-weight: bold;
  color: darkblue;
  line-height: 54px;
  height: 57px;
  margin-right: -1px;
  margin-top: -1px;
  margin-left: 20px;
  padding: 0;
  text-align: center;
  width: 57px;

  background-image: url("images/icons8-chip-40.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  cursor: pointer;
}

.selected-chip{
  background-color: greenyellow;
  border-radius: 15px !important;
  width: 60px;
  height: 60px;
}

.chip-bet{
  float: left;
  font-size: 15px;
  font-weight: bold;
  color: darkblue;
  line-height: 54px;
  height: 55px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 55px;

  background-image: url("images/icons8-chip-40.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  cursor: cursor;
}

.chip-bet-zero{
    margin-top: 55px !important;
}

.chip-bet-six{
  margin-left: 30px !important;
}

 button{
  
  margin-left: 20px;
  margin-bottom: 20px;

}

.spin-btn{
  margin-left: 22% !important;
  width: 170px;
}

.spin-control{
  width: 875px !important;
}

.wheel{

  margin-left: 10% !important;
  width: 875px !important;

}

.winner-cell{
  border: 5px solid rgb(231, 179, 7) !important; 
}

.green-simple{
  background-color: green;
  color: white;
  height: 171px;
  line-height: 170px;
}

.lastest-plays{
  opacity: 0.7;
  width: 350px;
}

.lastest-plays .list-group-item{
  padding: 6px;
}



@keyframes blinking {
  0% {
    background-color: red;
  }
  100% {
    background-color: #28a745;
  }
}

.blink {
  animation: blinking 1s infinite;
}


